import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import BlogIndex from "."

const CategoryPage = ({ data, pageContext }) => {
  const { category, subcategory } = pageContext
  const posts = data.allMarkdownRemark.nodes.filter(
    node =>
      node.frontmatter.main === category && node.frontmatter.sub === subcategory
  )

  return (
    <Layout>
      <BlogIndex posts={posts} />
    </Layout>
  )
}

export default CategoryPage

export const query = graphql`
  query ($category: String!, $subcategory: String!) {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: {
        frontmatter: { main: { eq: $category }, sub: { eq: $subcategory } }
      }
    ) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          main
          sub
        }
      }
    }
  }
`
